.outside_searchcar_boxmodel {
  background-color: rgb(252, 252, 252);
  padding: 100px 0px;
  margin-top: 100px;
}
.boxmodel {
  width: 70%;
  margin: auto;
  margin-top: 0px;
}
.boxmodel_body {
  background-color: #f8f4f4;
  padding: 10px;
  display: flex;
}

.imgresult {
  width: 60%;
  padding: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.imgresult img {
  width: 100%;
  height: auto;
}

.searchcar {
  width: 40%;
  background-color: white;
  overflow: hidden;
}
.downarrow,
.uparrow {
  background-color: #f8f4f4;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.uparrow {
  margin-bottom: 5px;
}
.downarrow {
  margin-top: 5px;
}
.uparrow img {
  transform: rotate(-90deg);
}
.downarrow img {
  transform: rotate(-90deg);
}
.search_card_col {
  height: 400px;
}
.search_card_col:hover {
  overflow-x: auto;
}
.search_card_col a {
  text-decoration: none;
}
.search_card {
  /*display: flex;*/
  width: 100%;
  padding: 20px 10px 20px 30px;
  margin-bottom: 2px;
  background-color: #f8f4f4;
}
.search_card:hover {
  position: relative;
  /*background-color: lightgrey;*/
  box-shadow: 15px 2px 25px #aaaaaa;
}

.card_title {
  width: 100%;
}
.card_title h2 {
  margin: 0;
  margin-bottom: 10px;
  color: rgba(93, 93, 93, 255);
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
}
.card_price_search {
  display: flex;
  align-items: center;
}
.card_price p {
  margin: 0;
  color: rgba(93, 93, 93, 255);
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  font-size: 12px;
}
.card_search {
  margin-left: auto;
  /* display: none; */
}
